.friends{
    grid-area: c;
    margin: 10px;
}

.error{
    width: 100%;
    background-color: #920f0f;
    color: #df6b6b;
    padding: 1%;
}

.friend{
    padding: 20px;
}

.friendPhoto{
    display: inline-block;
    vertical-align: middle;
    margin-right: 20px;
}

.friendPhoto img{
    width: 60px;
    height: 55px;
}

.friendName{
    display: inline-block;
    color: #000;
    font-size: 20px;
}

.friend_button{
    display: inline-block;
    float: right;
}

.friend__follow_button{
    background-color: #395d8b;
    border: 0;
    color: #fff;
    cursor: pointer;
    padding: 10px 20px;
}

.friend__unfollow_button{
    background-color: #c4c4c4;
    border: 1px solid #395d8b;
    color: #000;
    cursor: pointer;
    padding: 10px 20px;
}

.page{
    font-size: 18px;
    margin-left: 15px;
}

.page:hover{
    cursor: pointer;
}

.selectedPage{
    font-size: 18px;
    font-weight: bold;
    margin-left: 15px;
    text-decoration: underline;
}

.selectedPage:hover{
    cursor: pointer;
}

.prevPageButton{
    background-image: url('../assets/images/pageToggleButton.png');
    width: 32px;
    height: 32px;
    border: 0;
    background-size: contain;
    background-color: #fff;
    margin-left: -30px;
    vertical-align: middle;
    transition: 0.5s ease;
    position: absolute;
}

.nextPageButton{
    background-image: url('../assets/images/pageToggleButton.png');
    transform: rotate(180deg);
    width: 32px;
    height: 32px;
    border: 0;
    background-size: contain;
    background-color: #fff;
    margin-right: 10px;
    margin-left: 15px;
    vertical-align: middle;
    transition: 0.5s ease;
    position: absolute;
}

.prevPageButton:hover,
.nextPageButton:hover{
    width: 38px;
    height: 38px;
    transition: 0.2s ease;
}

@media screen and (max-width: 600px) {
    .friendPhoto img{
        width: 40px;
        height: 35px;
    }
    .friendName{
        font-size: 16px;
    }
    .friend__follow_button{
        padding: 5px 10px;
        margin: auto 0;
    }
    .friend__unfollow_button{
        padding: 5px 10px;
        margin: auto 0;
    }
}